.ditty-page {
  background: #fff;
  overflow: hidden;
  #wpcontent {
    display: flex;
    flex-direction: row;
    padding-left: 0;
  }
  #wpbody {
    flex: 1;
    width: 100%;
  }
  #wpbody-content {
    padding: 0;
    float: none;
  }
  #adminmenuwrap {
    height: 666px;
    overflow-y: scroll;
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
    -webkit-overflow-scrolling: touch;
  }
  #adminmenuwrap::-webkit-scrollbar {
    display: none;
  }
  #menu-posts-ditty {
    > .wp-submenu-wrap {
      border-left: 0;
    }
  }
}
